import React from 'react';
import runApp from '../run-app';
import SyncExceptionWindow from './sync-exception';
import OwnerExpiredWindow from './owner-expired';
import AccountLockedWindow from './account-locked';
import BatchUnlockWindow from './batch-unlock';

const params = new URL(window.location.href).searchParams;
const modal = params.get('modal');

switch (modal) {
  case 'SyncException':
    runApp(<SyncExceptionWindow />, ['getSyncExceptions', 'resolveSyncExceptions']);
    break;
  case 'OwnerExpired':
    const sandboxName = window.decodeURIComponent(params.get('sandboxName') || '');
    runApp(<OwnerExpiredWindow sandboxName={sandboxName} />, ['openWebHomePage']);
    break;
  case 'AccountLocked':
    runApp(<AccountLockedWindow />, ['packLog', 'openUnlockPage', 'closeWindow']);
    break;
  case 'BatchUnlock':
    runApp(<BatchUnlockWindow />, ['getLockedFiles', 'unlockFiles']);
    break;
  default:
    console.error('Unknown modal: ', modal);
    break;
}
