import { useCallback, useEffect, useState } from 'react';
import {
  SyncException,
  SyncExceptionGroup,
  getSyncExceptions,
  resolveSyncExceptions as resolve,
} from '../../java-api/common-window';

export default function useSyncExceptionModel() {
  const [loading, setLoading] = useState<boolean>(true);
  const [exceptions, setExceptions] = useState<SyncException[]>([]);
  const [exceptionGroupMap, setExceptionGroupMap] = useState<Map<
    number,
    SyncExceptionGroup
  > | null>(null);

  useEffect(() => {
    const fetchSyncExceptions = async () => {
      setLoading(true);
      const exceptions = await getSyncExceptions();
      setExceptions(exceptions);
      setExceptionGroupMap(groupExceptionsByReason(exceptions));
      setLoading(false);
    };

    fetchSyncExceptions();
  }, []);

  const resolveSyncExceptions = useCallback(
    async (selectedExceptions: SyncException[]) => {
      await resolve(selectedExceptions.map((se) => se.fullPath));
      const newExceptions = exceptions.filter((se) => !selectedExceptions.includes(se));
      setExceptions(newExceptions);
      setExceptionGroupMap(groupExceptionsByReason(newExceptions));
    },
    [exceptions]
  );

  const toggleGroup = useCallback(
    (group: SyncExceptionGroup) => {
      const newMap = groupExceptionsByReason(exceptions);
      if (newMap?.has(group.reasonId)) {
        const g = newMap?.get(group.reasonId);
        if (g) {
          g.folded = !group.folded;
          setExceptionGroupMap(newMap);
        }
      }
    },
    [exceptions]
  );

  return { loading, exceptions, exceptionGroupMap, resolveSyncExceptions, toggleGroup };
}

const groupExceptionsByReason = (exceptions: SyncException[]) => {
  const groupMap = new Map<number, SyncExceptionGroup>();
  exceptions.forEach((e) => {
    if (groupMap.has(e.reasonId)) {
      const group = groupMap.get(e.reasonId);
      group?.exceptions.push(e);
    } else {
      groupMap.set(e.reasonId, {
        reasonId: e.reasonId,
        reasonDisplay: e.reasonDisplay,
        exceptions: [e],
        folded: false,
      });
    }
  });
  return groupMap;
};
