// ***************************************************************************
// Sync Exception Api
// ***************************************************************************

import { importJavaPromise, importJavaPromiseNonJson } from '../../java-interop';

export type SyncException = {
  fileName: string; // Sync error file name
  parentDir: string; // Sync error parent directory
  reasonId: number; // Id of the sync error reason, can be used to group the sync errors in UI
  reasonDisplay: string; // The localized sync error reason string
  fullPath: string; // Full absolute of the sync error, can be used as key to resolve the sync error.
  timestamp: number; // The timestamp of when the sync error is created
  deleted: boolean; // If the sync error is deleted or not
};

export type SyncExceptionGroup = {
  reasonId: number; // Id of the sync error reason, can be used to group the sync errors in UI
  reasonDisplay: string; // The localized sync error reason string
  folded: boolean; // If group is folded or not
  exceptions: SyncException[];
};

export const getSyncExceptions = importJavaPromise<() => SyncException[]>('getSyncExceptions');
export const resolveSyncExceptions =
  importJavaPromiseNonJson<(exceptionPaths: string[]) => void>('resolveSyncExceptions');
