// ***************************************************************************
// Batch Unlock Api
// ***************************************************************************

import { importJavaFunc, importJavaPromise, importJavaPromiseNonJson } from '../../java-interop';

export type LockedFile = {
  fileName: string; // File name
  parentDir: string; // File parent directory
  fullPath: string; // Full absolute of the file, can be used as key to unlock the file.
  timestamp: number; // The timestamp of when the file is locked
  locker: string; // The person who locked this file
};

export const getLockedFiles = importJavaPromise<() => LockedFile[]>('getLockedFiles');
export const unlockFiles = importJavaPromiseNonJson<(filePaths: string[]) => void>('unlockFiles');
export const closeWindow = importJavaFunc<() => void>('closeWindow');
