import React, { ChangeEvent, useCallback, useState } from 'react';
import { useI18n } from '../../i18n';
import loading from '../../assets/common-window/loading.svg';
import warningIcon from '../../assets/warning_icon.svg';
import { openPath, openUrl } from '../../java-api/common-api';
import { Tooltip } from '../../components/tooltip';
import classNames from 'classnames';
import { LockedFile, closeWindow } from '../../java-api/common-window';
import useLockedFilesModel from '../../models/common-window/locked-files-model';

export default () => {
  const { $t } = useI18n();
  const [selectedFiles, setSelectedFiles] = useState<LockedFile[]>([]);

  const { loading: fetching, lockedFiles, unlockFiles, unlocking } = useLockedFilesModel();

  const selectFile = useCallback(
    (file: LockedFile) => {
      setSelectedFiles([...selectedFiles, file]);
    },
    [selectedFiles]
  );

  const unselectFile = useCallback(
    (file: LockedFile) => {
      selectedFiles.splice(selectedFiles.indexOf(file), 1);
      setSelectedFiles([...selectedFiles]);
    },
    [selectedFiles]
  );

  const onCheckBoxChanged = useCallback(
    (event: ChangeEvent<HTMLInputElement>, file: LockedFile) => {
      if (event.currentTarget.checked) {
        selectFile(file);
      } else {
        unselectFile(file);
      }
    },
    [selectFile, unselectFile]
  );

  const onFilenameClick = useCallback(
    (file: LockedFile) => {
      if (selectedFiles.includes(file)) {
        unselectFile(file);
      } else {
        selectFile(file);
      }
    },
    [selectedFiles, selectFile, unselectFile]
  );

  const onSelectAllChanged = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      if (event.currentTarget.checked) {
        setSelectedFiles(lockedFiles);
      } else {
        setSelectedFiles([]);
      }
    },
    [lockedFiles]
  );

  const renderFiles = useCallback(() => {
    return lockedFiles.map((file) => {
      return (
        <div
          className={classNames('lockedFile', {
            selected: selectedFiles.includes(file),
          })}
          key={file.fullPath}
        >
          <input
            type="checkbox"
            checked={selectedFiles.includes(file)}
            onChange={(event) => onCheckBoxChanged(event, file)}
          />
          <Tooltip title={file.fileName} width={'200px'}>
            <div className="name" onClick={() => onFilenameClick(file)}>
              {file.fileName}
            </div>
          </Tooltip>
          <div className="path" onClick={() => openPath(file.fullPath)}>
            <Tooltip title={file.fullPath} width={'200px'}>
              <span>{file.parentDir}</span>
            </Tooltip>
          </div>
          <div className="locker">
            <Tooltip title={file.locker} width={'200px'} placement="left">
              <span>{file.locker.split('<')[0]}</span>
            </Tooltip>
          </div>
        </div>
      );
    });
  }, [lockedFiles, selectedFiles, onCheckBoxChanged, onFilenameClick]);

  const unlock = useCallback(async () => {
    if (selectedFiles.length) {
      await unlockFiles(selectedFiles);
      setSelectedFiles([]);
    }
  }, [unlockFiles, selectedFiles]);

  return (
    <div className="batch-unlock-window">
      <div className="top-panel">
        <img src={warningIcon} alt="" />
        <div className="text">
          <div className="title">{$t('syncFolder.lockedFile.title')}</div>
          <div className="desc">{$t('syncFolder.lockedFile.desc')}</div>
        </div>
      </div>
      <div className="content-panel">
        {lockedFiles.length || !fetching ? (
          <div className="lockedFile-list">
            <div className="top-bar">
              <input type="checkbox" onChange={onSelectAllChanged} />
              <div className="name">{$t('syncFolder.lockedFile.filename')}</div>
              <div className="path">{$t('syncFolder.lockedFile.path')}</div>
              <div className="locker">{$t('syncFolder.lockedFile.locker')}</div>
            </div>
            {renderFiles()}
          </div>
        ) : (
          <div className="scanning">
            <img src={loading} alt="" />
            <div className="scanning-text">{$t('syncFolder.lockedFile.scanning')}</div>
          </div>
        )}
      </div>
      <div className="bottom-panel">
        <div className="why" onClick={() => openUrl('https://help.jianguoyun.com/?p=4054')}>
          {$t('syncFolder.lockedFile.help')}
        </div>
        <div className="buttons">
          <div className={classNames('close')} onClick={() => closeWindow()}>
            {$t('syncFolder.lockedFile.later')}
          </div>
          <div
            className={classNames('resolve', { disabled: selectedFiles.length === 0 || unlocking })}
            onClick={unlock}
          >
            {unlocking
              ? $t('syncFolder.lockedFile.unlocking')
              : $t('syncFolder.lockedFile.forceUnlock')}
          </div>
        </div>
      </div>
    </div>
  );
};
