import React, { ChangeEvent, useCallback, useState } from 'react';
import { useI18n } from '../../i18n';
import syncErrorIcon from '../../assets/common-window/view_sync_errors_icon.svg';
import loading from '../../assets/common-window/loading.svg';
import pathIcon from '../../assets/common-window/path.svg';
import arrow from '../../assets/right-arrow.svg';
import useSyncExceptionModel from '../../models/common-window/sync-exception-model';
import { SyncException, SyncExceptionGroup } from '../../java-api/common-window';
import { openPath, openUrl } from '../../java-api/common-api';
import { Tooltip } from '../../components/tooltip';
import classNames from 'classnames';

export default () => {
  const { $t } = useI18n();
  const [selectedExceptions, setSelectedExceptions] = useState<SyncException[]>([]);

  const {
    loading: exceptionsLoading,
    exceptions,
    exceptionGroupMap,
    resolveSyncExceptions,
    toggleGroup,
  } = useSyncExceptionModel();

  const selectException = useCallback(
    (se: SyncException) => {
      setSelectedExceptions([...selectedExceptions, se]);
    },
    [selectedExceptions]
  );

  const unselectException = useCallback(
    (se: SyncException) => {
      selectedExceptions.splice(selectedExceptions.indexOf(se), 1);
      setSelectedExceptions([...selectedExceptions]);
    },
    [selectedExceptions]
  );

  const onCheckBoxChanged = useCallback(
    (event: ChangeEvent<HTMLInputElement>, se: SyncException) => {
      if (event.currentTarget.checked) {
        selectException(se);
      } else {
        unselectException(se);
      }
    },
    [selectException, unselectException]
  );

  const onFilenameClick = useCallback(
    (se: SyncException) => {
      if (selectedExceptions.includes(se)) {
        unselectException(se);
      } else {
        selectException(se);
      }
    },
    [selectedExceptions, selectException, unselectException]
  );

  const onSelectAllChanged = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      if (event.currentTarget.checked) {
        setSelectedExceptions(exceptions);
      } else {
        setSelectedExceptions([]);
      }
    },
    [exceptions]
  );

  const renderExceptions = useCallback(() => {
    const groups: SyncExceptionGroup[] = [];
    exceptionGroupMap?.forEach((group) => {
      groups.push(group);
    });
    return groups.map((group) => {
      return (
        <div className="exception-group" key={group.reasonId}>
          <div className="exception-group-header">
            <img
              src={arrow}
              alt=""
              className={classNames('toggler', { folded: group.folded })}
              onClick={() => toggleGroup(group)}
            />
            <div className="exception-group-reason">{group.reasonDisplay}</div>
          </div>
          {!group.folded && (
            <div className="exception-group-content">
              {group.exceptions.map((exception) => {
                // const formattedTimestamp = new Date(exception.timestamp * 1000).toLocaleString();
                return (
                  <div
                    className={classNames('exception', {
                      selected: selectedExceptions.includes(exception),
                    })}
                    key={exception.fullPath}
                  >
                    <input
                      type="checkbox"
                      checked={selectedExceptions.includes(exception)}
                      onChange={(event) => onCheckBoxChanged(event, exception)}
                      disabled={exception.deleted}
                    />
                    <Tooltip title={exception.fileName} width={'200px'}>
                      <div className="name" onClick={() => onFilenameClick(exception)}>
                        {exception.fileName}
                      </div>
                    </Tooltip>
                    <Tooltip title={exception.reasonDisplay} width={'200px'}>
                      <div className="reason">{exception.reasonDisplay}</div>
                    </Tooltip>
                    <div className="path" onClick={() => openPath(exception.fullPath)}>
                      <img src={pathIcon} alt=""></img>
                      <Tooltip title={exception.parentDir} width={'200px'}>
                        <span>{exception.parentDir}</span>
                      </Tooltip>
                    </div>
                    {/* <Tooltip title={formattedTimestamp} width={'200px'}>
                      <div className="time">{formattedTimestamp}</div>
                    </Tooltip> */}
                  </div>
                );
              })}
            </div>
          )}
        </div>
      );
    });
  }, [exceptionGroupMap, selectedExceptions, onCheckBoxChanged, toggleGroup, onFilenameClick]);

  const resolve = useCallback(async () => {
    if (selectedExceptions.length) {
      await resolveSyncExceptions(selectedExceptions);
      setSelectedExceptions([]);
    }
  }, [resolveSyncExceptions, selectedExceptions]);

  return (
    <div className="sync-exception-window">
      <div className="top-panel">
        <img src={syncErrorIcon} alt="" />
        <div className="text">
          <div className="title">{$t('syncFolder.exception.title')}</div>
          <div className="desc">{$t('syncFolder.exception.desc')}</div>
        </div>
      </div>
      <div className="content-panel">
        {exceptions.length || !exceptionsLoading ? (
          <div className="exception-list">
            <div className="top-bar">
              <input type="checkbox" onChange={onSelectAllChanged} />
              <div className="name">{$t('syncFolder.exception.filename')}</div>
              <div className="reason">{$t('syncFolder.exception.reason')}</div>
              <div className="path">{$t('syncFolder.exception.path')}</div>
              {/* <div className="time">{$t('syncFolder.exception.time')}</div> */}
            </div>
            {renderExceptions()}
          </div>
        ) : (
          <div className="scanning">
            <img src={loading} alt="" />
            <div className="scanning-text">{$t('syncFolder.exception.scanning')}</div>
          </div>
        )}
      </div>
      <div className="bottom-panel">
        <div className="why" onClick={() => openUrl('https://help.jianguoyun.com/?p=4159')}>
          {$t('syncFolder.exception.why')}
        </div>
        <div
          className={classNames('resolve', { disabled: selectedExceptions.length === 0 })}
          onClick={resolve}
        >
          {$t('syncFolder.exception.resolve')}
        </div>
      </div>
    </div>
  );
};
