import { useCallback, useEffect, useState } from 'react';
import { LockedFile, getLockedFiles, unlockFiles as unlock } from '../../java-api/common-window';

export default function useLockedFilesModel() {
  const [loading, setLoading] = useState<boolean>(true);
  const [lockedFiles, setLockedFiles] = useState<LockedFile[]>([]);
  const [unlocking, setUnlocking] = useState<boolean>(false);

  useEffect(() => {
    const fetchLockedFiles = async () => {
      setLoading(true);
      const files = await getLockedFiles();
      setLockedFiles(files);
      setLoading(false);
    };

    fetchLockedFiles();
  }, []);

  const unlockFiles = useCallback(
    async (selectedFiles: LockedFile[]) => {
      setUnlocking(true);
      await unlock(selectedFiles.map((file) => file.fullPath));
      setUnlocking(false);
      const remainedFiles = lockedFiles.filter((file) => !selectedFiles.includes(file));
      setLockedFiles(remainedFiles);
    },
    [lockedFiles]
  );

  return { loading, lockedFiles, unlockFiles, unlocking };
}
