import React, { ChangeEvent, useCallback, useState } from 'react';
import { useI18n } from '../../i18n';
import warningIcon from '../../assets/warning_icon.svg';
import { closeWindow, openUnlockPage, packLog } from '../../java-api/common-window/account-locked-api';

export default () => {
  const { $t } = useI18n();

  const [withDataBase, setWithDataBase] = useState<boolean>(true);

  const onCheckBoxChanged = (event: ChangeEvent<HTMLInputElement>) => {
    setWithDataBase(event.currentTarget.checked);
  };

  const onPackageLog = useCallback(async () => {
    await packLog(withDataBase);
  }, [withDataBase]);

  return (
    <div className="account-locked">
      <div className="warning-icon">
        <img src={warningIcon} alt="" />
      </div>
      <div className="content">
        <div className="title">{$t('account.locked.title')}</div>
        <div className="desc">{$t('account.locked.desc')}</div>
        <div className="step-1">{$t('account.locked.step-1')}</div>
        <div className="step-2">{$t('account.locked.step-2')}</div>
        <div className="with-database">
          <input type="checkbox" checked={withDataBase} onChange={onCheckBoxChanged} />
          <span>{$t('button.packageLogWithDatabase')}</span>
        </div>
      </div>
      <div className="package-log" onClick={() => onPackageLog()}>
        {$t('button.packageLog')}
      </div>
      <div className="cancel" onClick={() => closeWindow()}>
        {$t('button.cancel')}
      </div>
      <div className="request-unlock" onClick={() => openUnlockPage()}>
        {$t('account.locked.requestUnlock')}
      </div>
    </div>
  );
};
